/* General container styling */
.container {
  max-width: 1140px;
  margin: auto;
}

.plans:hover {
  transform: scale(1.02);
  transition: all 0.3s;
}

.pricing-header img.membership {
  width: 50px;
  margin-left: 10px;
  vertical-align: middle;
}

/* Responsive font sizes */
.card-price {
  font-size: 1.25rem; /* Base font size */
  font-weight: 600;
  margin-bottom: 1rem;
}

.card-feature {
  margin: 0.5rem 0;
  font-size: 1rem; /* Base font size for features */
}

.card-title {
  font-size: 1.5rem; /* Title font size */
  font-weight: 700;
}

.btn-plan {
  margin-top: 1rem;
  width: 100%;
}

